import Vue from 'vue'
import Router from 'vue-router'
import login from '@/view/login/login'
import mainpage from '@/view/mainpage/mainpage'
import home from '@/view/mainpage/mcontent/home/index'
import person from '@/view/mainpage/mcontent/person/index'
import manage from '@/view/mainpage/mcontent/manage/index'
import other from '@/view/mainpage/mcontent/manage/index'
import test1 from '@/view/mainpage/mcontent/person/test1'
import test2 from '@/view/mainpage/mcontent/person/test2'
import test3 from '@/view/mainpage/mcontent/manage/test3'
import test4 from '@/view/mainpage/mcontent/manage/test4'
import test5 from '@/view/mainpage/mcontent/other/test5'
import test6 from '@/view/mainpage/mcontent/other/test6'
import test7 from '@/view/mainpage/mcontent/other/test7'
import test8 from '@/view/mainpage/mcontent/other/test8'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: login
    },
    {
      path: '/mainpage',
      name: 'mainpage',
      component: mainpage,
      children: [
        {
          path: '/home',
          name: 'home',
          component: home
        },
        {
          path: '/person',
          name: 'person',
          component: person,
          children: [
            {
              path: '/test1',
              name: 'test1',
              component: test1
            },{
              path: '/test2',
              name: 'test2',
              component: test2
            }
          ]
        },{
          path: '/manage',
          name: 'manage',
          component: manage,
          children: [
            {
              path: '/test3',
              name: 'test3',
              component: test3
            },{
              path: '/test4',
              name: 'test4',
              component: test4
            }
          ]
        },{
          path: '/other',
          name: 'other',
          component: other,
          children: [
            {
              path: '/test5',
              name: 'test5',
              component: test5
            },{
              path: '/test6',
              name: 'test6',
              component: test6
            },{
              path: '/test7',
              name: 'test7',
              component: test7
            },{
              path: '/test8',
              name: 'test8',
              component: test8
            }
          ]
        }
      ]
    }
  ]
})



