<template>
  <Menu :theme="themed" active-name="1-2" width="auto" :open-names="[menuData[0].name]" @on-select="menuSelect">
    <Submenu v-for="item in menuData" :name="item.name" :key="item.name">
      <template slot="title">  
        <Icon :type="item.icon"></Icon>{{item.label}}  
      </template>
      <menu-item v-for="mi in item.menuitems" :name="mi.name" :key="mi.name">{{mi.label}}</menu-item>  
    </Submenu>
  </Menu>
</template>

<script>
import config from '@/components/config/config.js' 
export default {
  name: 'leftmenus',
  data () {
    return {
      themed: 'light',
      menuData: config.leftMenusConfig,
      breadCrumbs: []
    }
  },
  methods: {
    menuSelect: function(name){
      this.$router.push(name);
    }
  },
  watch: {
    '$route' (newRoute) {
      console.log(newRoute.matched);
      this.$store.dispatch("setBreadCrumbs",newRoute.matched);
    }
  }
}
</script>



