<template>
  <div class="container">
    <div class="content">
      <div class="login-input">
        <input v-model="username"/>
      </div>
      <div class="login-input">
        <input v-model="userpwd"/>
      </div>
      <Button @click="login">登陆</Button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'login',
  data () {
    return {
      username: '',
      userpwd: ''
    }
  },
  methods: {
    login: function(){
      this.$store.dispatch("setLoginInfo", this.username);
      this.$router.push("/home");
    }
  }      
}
</script>

<style scoped>
  .container {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -150px;
    margin-left: -200px;
    width: 400px;
    height: 300px;
    background-color: #2d8cf0;
  }
  .content {
    padding-top: 50px;
  }
  .content input {
    height: 40px;
    width: 350px;
    margin-top: 50px;
    outline: none;
  }
  .content button {
    margin-top: 20px;
    width: 350px;
  }
</style>



