<template>
  <div class="layout">
    <Layout>
      <Header>
        <theader></theader>
      </Header>
      <Layout>
        <Sider hide-trigger :style="{background: '#fff'}">
          <leftmenus>
          </leftmenus>
        </Sider>
        <Layout :style="{padding: '0 24px 24px'}">
          <Breadcrumb :style="{margin: '24px 0'}">
            <BreadcrumbItem></BreadcrumbItem>
          </Breadcrumb>
          <Content :style="{padding: '24px', minHeight: '400px', background: '#fff'}">
            <router-view/>
          </Content>
        </Layout>
      </Layout>
    </Layout>
  </div>
</template>

<script>
  import leftmenus from "@/view/mainpage/leftmenus/leftmenus.vue";
  import theader from "@/view/mainpage/theader/theader.vue";
  export default {
    name: 'mainpage',
    data() {
      return {

      }
    },
    components: {
      theader: theader,
      leftmenus: leftmenus
    }
  }
</script>

<style scoped>
  .layout{
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
  }
</style>




