import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

var store = new Vuex.Store({
  state: {
    loginInfo: '',
    breadCrumbsList: 'home'
  },

  getters: {
    getLoginInfo(state){
      return state.loginInfo;
    },
    getBreadCrumbs(state){
      return state.breadCrumbsList;
    }
  },

  actions: {
    setLoginInfo({commit,state},name){
      commit('setLoginInfo',name);
    },
    setBreadCrumbs({commit,state},name){
      commit('setBreadCrumbs',name);
    }
  },

  mutations: {
    setLoginInfo(state,name){
      state.loginInfo = name;
    },
    setBreadCrumbs(state,name){
      state.breadCrumbsList = name;
    }
  }
});

export default store;


